header {
    text-align: center;
    padding: 20px 0;
}


.intro {
    text-align: center;
    padding: 20px 0;
}


.slider-section {
    margin: 20px 0;
}


.description {
    text-align: center;
    padding: 20px 0;
}


.features {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 20px 0;
}

.feature {
    text-align: center;
    margin: 0 10px;
}

.feature img {
    width: 100px;
    height: 100px;
}


@media (max-width: 768px) {
    .features {
        flex-direction: column;
        align-items: center;
    }

    .feature {
        margin: 10px 0;
    }
}

.loadPrice{
    position: fixed;
    right: 0;
    bottom: 70px;
    /* background: var(--background); */
    border-radius: 17px 0 0 17px;
}

.loading{
    position: fixed;
    z-index: 999;
    top: 50%;
    left: 47%;    
   -webkit-transform: translate(-50%, -50%); /* Safari  CSS */
   transform: translate(-50%, -50%);

}

.container-submit{
    display: flex;
    justify-content: space-around;
    padding: 2px;    
}

.loading-submit{
    font-size: 2em; 
    text-shadow: 1px 1px 2px rgba(255, 255, 255, 0.8), 
     2px 2px 4px rgba(0, 0, 0, 0.2);  
    padding: 10px;
    border: 2px solid ;
    background-color: #f0f0f0;
    border-radius: 17px;
    cursor: pointer;
}

.loading-submit {
    animation: loading 2s infinite alternate;
}

@keyframes loading {
    0% {
        background-color: #91eaa8;
        color: #140e0f;
    }
    50% {
        background-color: #d5f3d6;
        transform: scale(0.95);
    }
    
    100% {
        background-color: #9ce4c6;
        color: #025d40;
        transform: scale(1);
    }
}

.loading-submit:hover {
    background-color: #45a049;
    transform: scale(1.1);
}

.filters-field{
  display: flex;
  align-items: center;
  padding: 10px;
  border: 2px solid #ccc;
  cursor: pointer;
  transition: background-color 0.3s ease; 
  border-radius: 20px 20px 120px 0; 
}

.filters-field:hover{
   border-radius: 10px 10px 0 0;    
}

.selected-field{
    background-color: green; 
    color: #fff;
}

